/* General styling for the model overview page */
.model-overview {
  background-color: #f7f9fc;
}

.model-overview > .model {
  padding: 30px 60px;
  background: #ffffff;
  margin: 20px 30px;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}

.model-overview h1 {
  font-size: 36px;
  color: #333333;
  text-align: center;
}

.model-overview h2 {
  font-size: 28px;
}

.model-overview p {
  color: #666666;
}

.model-overview a {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 16px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 10px;
}

.model-overview a:hover {
  background-color: #45a049;
}
